<template>
	<view class="order-list-wrap">
		<view class="order-list">
			<view
				v-for="(order, index) in orders"
				:key="order.id"
				class="order-item"
			>
				<view class="order-header">
					<view>
						<radio :checked="checkedMaps[order.id]" :color="themeColor" @click.stop="onCheckedChange(index, $event)" />
						<text>运单号：{{ order.code }}</text>
					</view>
					<view class="order-date">2022.03.15</view>
				</view>
				
				<view class="order-content-body">
					<order-delivery-info :order="order"></order-delivery-info>
					<view class="order-goods-info">
						<text>货物信息：10kg｜10立方｜10件</text>
					</view>
				</view>
				
				<view class="order-fee-info">
					<text class="order-payway">现付</text>
					<text class="order-fee-amount">费用：¥100.00</text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import { themeColor } from 'uni.scss'
	import OrderDeliveryInfo from '@/components/order-parts/order-delivery-info/index.vue'
	
	export default {
		name: 'OrderList',
		components: {
			OrderDeliveryInfo
		},
		props: {
			orders: {
				type: Array,
				default: () => ([])
			},
			checkedMaps: {
				type: Object,
				default: () => ({})
			}
		},
		data() {
			return {
				themeColor
			}
		},
		methods: {
			onClickOrder(index) {
				const orderItem = this.orders[index]
				uni.navigateTo({
					url: `/pages/delivery-order-detail/index?orderId=${orderItem.id}`
				})
			},
			onCheckedChange(index, event) {
				this.$emit('checked-change', this.orders[index].id)
			}
		}
	}
</script>

<style lang="scss" scoped>
	.order-list {
	}
	
	.order-item {
		display: block;
		background-color: #fff;
		box-shadow: 0px 2upx 16upx 0px rgba(0,0,0,0.0500);
		border-radius: 10upx;
		padding: 0 20upx;
		
		& + .order-item {
			margin-top: 30upx;
		}
	}
	
	.order-header {
		@include flex-c-center;
		padding: 20upx 0;
		color: #A5A8BC;
		font-size: 26upx;
		justify-content: space-between;
		border-bottom: 2upx solid $border-color;
		
		radio {
			transform: scale(.7);
		}
	}
	
	.order-goods-info {
		color: #666;
		padding-bottom: 20upx;
		border-bottom: 2upx solid $border-color;
	}
	
	.order-fee-info {
		padding: 20upx 0;
		color: $theme-color;
		@include flex-between;
		font-size: 28upx;
	}
	
	.order-fee-amount {
		font-weight: 500;
	}
	
</style>
